html,
body {
  height: 100vh;
  font-family: 'Montserrat', sans-serif;
}

.signBody {
  display: flex;
  -ms-flex-align: center;
  -ms-flex-pack: center;
  align-items: center;
  justify-content: center;
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: #f5f5f5;
}

.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: 0 auto;
}
.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
 
}
.form-control:focus{
  box-shadow: none;
}

.form-signin #inputEmail{
  margin-bottom: -1px;
}
.form-signin #inputPassword{
  margin-bottom: 10px;
}

